import React, { Component } from "react";
import { Row, Col, Image, Button, Container } from "react-bootstrap";

import "../styles/landing.css";
export default class Landing extends Component {
  render() {
    return (
      <Container fluid>
        <Row className="full-h">
          <Col className="h-100 d-flex justify-content-center text-center landing-background">
            <Row className="align-self-center">
              <Col className="text-center">
                <Image
                  className="d-md-none animated fadeInRight delay-2s shadow mb-3"
                  height="150"
                  roundedCircle
                  src={process.env.PUBLIC_URL + "/img/profile.jpg"}
                />

                <h1 className="animated fadeInLeft display-4 font-weight-bolder">
                  Exequiel Mleziva
                </h1>
                <p className="animated fadeInLeft delay-1s font-weight-bold">
                  full-stack developer
                </p>
                <Button
                  className="animated fadeInLeft delay-2s font-weight-bold p-3 px-5 mt-5 shadow"
                  href="/files/cv-exequiel-mleziva.pdf"
                  download
                >
                  <i className="fas fa-file-download mr-3"></i>
                  download my resume
                </Button>
              </Col>
            </Row>
          </Col>

          <div className="d-none d-md-block divider-y shadow" />

          <Col className="d-none d-md-block my-auto text-center">
            <Image
              className="animated fadeInRight delay-2s shadow p-0 m-0"
              height="300"
              roundedCircle
              src={process.env.PUBLIC_URL + "/img/profile.jpg"}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}
