import React, { Component } from "react";
import { ProgressBar } from "react-bootstrap";

export default class SkillBar extends Component {
  render() {
    return (
      <div className="shadow p-3 rounded progress-card">
        <h6 className="font-weight-bold">{this.props.skill}</h6>

        <div className="bar">
          <ProgressBar
            variant="info"
            now={this.props.percentage}
            label={`${this.props.percentage} %`}
          />
        </div>
      </div>
    );
  }
}
