import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import ReactVisibilitySensor from "react-visibility-sensor";

import SkillBar from "./SkillBar";

import "../styles/skills.css";

export default class Skills extends Component {
  state = {
    html: 0,
    css: 0,
    scss: 0,
    reactjs: 0,
    javascript: 0,
    nodejs: 0,
    mongodb: 0,
  };

  enterOnScreen = (isVisible) => {
    if (isVisible) {
      this.setState({
        html: 80,
        css: 80,
        scss: 40,
        reactjs: 80,
        javascript: 50,
        nodejs: 30,
        mongodb: 30,
      });
    }
  };

  render() {
    return (
      <ReactVisibilitySensor
        partialVisibility={true}
        onChange={this.enterOnScreen}
      >
        <Container fluid className="full-h skills-background">
          <Row className="h-100">
            <Col className="d-none d-md-block my-auto text-center">
              <h1 className="display-4 font-weight-bolder wow fadeInLeft">
                skills{" "}
                <span role="img" aria-label="skills">
                  ðŸ”§
                </span>
              </h1>
            </Col>

            <Col className="my-auto">
              <Row className="mb-4 d-block d-md-none">
                <Col>
                  <h1 className="display-4 font-weight-bolder wow fadeInLeft">
                    skills{" "}
                    <span role="img" aria-label="skills">
                      ðŸ”§
                    </span>
                  </h1>
                </Col>
              </Row>
              <div className="mx-md-5">
                <Row>
                  <Col className="wow fadeInRight">
                    <SkillBar skill="HTML" percentage={this.state.html} />
                  </Col>
                </Row>

                <Row className="mt-4">
                  <Col className="wow fadeInRight">
                    <SkillBar skill="CSS3" percentage={this.state.css} />
                  </Col>
                  <Col className="wow fadeInRight">
                    <SkillBar skill="SCSS" percentage={this.state.scss} />
                  </Col>
                </Row>

                <Row className="mt-4">
                  <Col className="wow fadeInRight">
                    <SkillBar
                      skill="JavaScript"
                      percentage={this.state.javascript}
                    />
                  </Col>
                </Row>

                <Row className="mt-4">
                  <Col className="wow fadeInRight">
                    <SkillBar
                      skill="React.js"
                      percentage={this.state.reactjs}
                    />
                  </Col>
                  <Col className="wow fadeInRight">
                    <SkillBar skill="Node.Js" percentage={this.state.nodejs} />
                  </Col>
                </Row>

                <Row className="mt-4">
                  <Col className="wow fadeInRight">
                    <SkillBar skill="MongoDb" percentage={this.state.mongodb} />
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </Container>
      </ReactVisibilitySensor>
    );
  }
}
