import React from "react";
import WOW from "wowjs";

import Landing from "./components/Landing";
import About from "./components/About";
import Skills from "./components/Skills";
import Contact from "./components/Contact";

import "./styles/divider.css";

new WOW.WOW({ mobile: false }).init();

function App() {
  return (
    <div className="App">
      <section className="landing full-viewport">
        <Landing />
      </section>

      <div className="divider-x shadow" />

      <section className="about full-viewport">
        <About />
      </section>

      <div className="divider-x shadow" />

      <section className="skills full-viewport">
        <Skills />
      </section>

      <section className="contact full-viewport">
        <Contact />
      </section>
    </div>
  );
}

export default App;
